<template>
  <section>
    <h2>My Friends</h2>
    <ul>
      <friend-contact 
      v-for="friend in friends"
        :key="friend.id"
        :id="friend.id"
        :name="friend.name"
        :phone="friend.phone"
        :email="friend.email"
        :isFavorite="friend.isFavorite"
        @toggle-favorite="toggleFavoriteStatus"
      ></friend-contact>
    </ul>
  </section>
</template>

<script>
export default {
  data() {
    return {
      friends: [
        {
          id: "jonas",
          name: "Jonas Jonaitis",
          phone: 132549765,
          email: "jonas@jonas.lt",
          isFavorite: true
        },
        {
          id: "petras",
          name: "Petras Petraitis",
          phone: 132549765,
          email: "petras@petras.lt",
          isFavorite: true
        },
      ],
    };
  },
  methods: {
      toggleFavoriteStatus(friendId) {
          console.log(friendId)
          const identifiedFriend = this.friends.find(friend => friend.id === friendId);
        //   console.log('Suradau: ', identifiedFriend)
          identifiedFriend.isFavorite = !identifiedFriend.isFavorite;
      }
  },
  computed: {},
};
</script>

